
















import Vue from 'vue';
import Component from 'vue-class-component';
import {Prop} from 'vue-property-decorator';

@Component({
  name: 'CodeBox'
})
export default class CodeBox extends Vue {
  @Prop(String)
  public code: string;
  @Prop(String)
  public markdown: string;
  public showCode = false;

  public copied() {
    this.$message.success('已复制');
  }
}
